@tailwind base;
@tailwind components;
.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 30px;
  background-color: #41544a;
  color: #fff;
  font-size: 12px;
}
.top-bar__contact {
  display: flex;
  align-items: center;
  column-gap: 30px;
}
.top-bar__info {
  display: flex;
  align-items: center;
}
.top-bar__info-icon {
  margin-right: 8px;
}
.top-bar__dropdown-group {
  display: flex;
  align-items: center;
  column-gap: 38px;
}
.top-bar__dropdown {
  display: flex;
}
.top-bar__dropdown-name {
  opacity: 70%;
  margin-right: 12px;
}
.top-bar__dropdown-arrow {
  color: #fff;
  margin-left: 10px;
}

.logo {
  display: flex;
  align-items: center;
}
.logo__img {
  margin-right: 15px;
  width: 50px;
  height: 60px;
}
.logo__heading {
  font-size: 28px;
  line-height: 34.5px;
  font-weight: bolder;
}
.logo__subheading {
  color: #86BC42;
  font-size: 12px;
  text-align: end;
}

.search-bar {
  max-width: 750px;
  width: 100%;
  border: 1px solid #ebebeb;
  border-radius: 4px;
  display: flex;
}
.search-bar__drop-down {
  padding: 14px 22px;
  position: relative;
  font-size: 14px;
}
.search-bar__drop-down::after {
  content: "";
  width: 1px;
  height: 22px;
  background-color: #ebebeb;
  display: inline-block;
  position: absolute;
  right: 0px;
  top: 50%;
  transform: translateY(-50%);
}
.search-bar__drop-down-text {
  font-weight: bold;
  margin-right: 28px;
}
.search-bar__input {
  flex-grow: 1;
  padding: 14px 22px;
  font-size: 14px;
}
.search-bar__input::placeholder {
  color: #4f4f4f;
}
.search-bar__submit {
  padding: 14px 22px;
  background-color: #86bc42;
  color: #fff;
  border-radius: 0 4px 4px 0;
  font-size: 20px;
  margin: -1px -1px -1px 0;
}

.categories-button {
  border-radius: 4px;
  background-color: #86bc42;
  padding: 16px 21px;
  color: #fff;
  display: flex;
  align-items: center;
}
.categories-button__nav-icon {
  font-size: 25px;
}
.categories-button__text {
  font-size: 12px;
  font-weight: bold;
  margin-left: 22px;
  margin-right: 36px;
}
.categories-button__arrow {
  font-size: 12px;
}

.account-info {
  display: flex;
  column-gap: 30px;
  font-size: 20px;
}
.account-info__icon {
  display: flex;
  align-items: center;
  position: relative;
}
.account-info__badge {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background-color: #86bc42;
  color: #fff;
  font-size: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -6px;
  left: -9px;
}
.account-info__cart-total-price {
  font-size: 13px;
  font-weight: 700;
  color: #121212;
  margin-left: 10px;
}

.nav-bar {
  display: flex;
  column-gap: 20px;
  align-items: flex-end;
}
.nav-bar__menu {
  display: flex;
  column-gap: 10px;
  align-items: center;
  font-weight: 700;
  font-size: 13px;
  color: #121212;
  padding-top: 25px;
  border-top: 2px solid transparent;
  transition: all 0.25s ease-in;
}
.nav-bar__menu:hover {
  border-color: #86bc42;
  color: #86bc42;
}
.nav-bar__arrow-icon {
  font-size: 8px;
}
.nav-bar__menu--active {
  color: #86bc42;
  border-color: #86bc42;
}

.hotline {
  color: #121212;
  display: flex;
  align-items: center;
  font-size: 13px;
  font-weight: 700;
}
.hotline__text {
  margin-left: 10px;
  margin-right: 2px;
}
.hotline__number {
  color: #86bc42;
}

.card-category {
  text-transform: uppercase;
  display: inline-flex;
  flex-direction: column;
  row-gap: 20px;
  align-items: center;
}
.card-category:hover .card-category__thumbnail {
  border-color: #86bc42;
}
.card-category:hover .card-category__name {
  color: #86bc42;
}
.card-category__thumbnail {
  transition: all 0.25s ease-in;
  width: 125px;
  height: 125px;
  border: 1px solid #ebebeb;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.card-category__thumbnail--active {
  border-color: #86bc42;
}
.card-category__name {
  transition: all 0.25s ease-in;
  font-size: 14px;
  font-weight: 700;
}
.card-category__name--active {
  color: #86bc42;
}

.hero {
  background-image: url(/public/images/background.svg);
  height: 600px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.hero__intro {
  color: #41544a;
  width: 50%;
}
.hero__text-1 {
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 0.1em;
}
.hero__text-2 {
  font-size: 60px;
  font-weight: 700;
  margin: 10px 0;
}
.hero__text-2-separate {
  color: #86bc42;
  font-weight: 400;
}
.hero__text-3 {
  font-size: 16px;
  font-weight: 400;
  margin-bottom: 30px;
}
.hero__text-3-separate {
  font-weight: 700;
}
.hero__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  padding: 17px 40px;
  background-color: #41544a;
  border-radius: 4px;
}
.hero__farmer {
  height: 100%;
}

.promotion-big {
  background-image: url(/source/images/promotion-background.svg);
  text-transform: uppercase;
  font-weight: 700;
  color: #fff;
  width: 630px;
  height: 300px;
  border: 1px solid transparent;
  border-radius: 4px;
  padding-top: 84px;
  padding-left: 40px;
}
.promotion-big__text-1 {
  font-size: 12px;
  letter-spacing: 0.1em;
}
.promotion-big__text-2 {
  font-size: 29px;
  font-weight: 600;
  margin-top: 16px;
  margin-bottom: 14px;
}
.promotion-big__btn {
  transition: all 0.25s ease-in;
  position: relative;
  font-size: 14px;
  padding-bottom: 8px;
}
.promotion-big__btn:hover {
  color: #41544a;
}
.promotion-big__btn:hover .promotion-big__btn-effect {
  background-color: #41544a;
}
.promotion-big__btn-effect {
  transition: all 0.25s ease-in;
  width: 50px;
  height: 2px;
  background-color: #fff;
  position: absolute;
  bottom: 0px;
  left: 0;
}

.promotion-small {
  background: url(/source/images/small-promotion-background-1.svg) no-repeat;
  text-transform: uppercase;
  font-weight: 700;
  color: #121212;
  width: 300px;
  height: 300px;
  border: 1px solid transparent;
  border-radius: 4px;
  padding-top: 84px;
  padding-left: 30px;
}
.promotion-small__text-1 {
  font-size: 12px;
  letter-spacing: 0.1em;
}
.promotion-small__text-2 {
  font-size: 29px;
  font-weight: 600;
  margin-top: 16px;
  margin-bottom: 14px;
}
.promotion-small__text-separate {
  color: #849d6a;
}
.promotion-small__btn {
  transition: all 0.25s ease-in;
  position: relative;
  font-size: 14px;
  color: #41544a;
  padding-bottom: 8px;
}
.promotion-small__btn:hover {
  color: #fff;
}
.promotion-small__btn:hover .promotion-small__btn-effect {
  background-color: #fff;
}
.promotion-small__btn-effect {
  transition: all 0.25s ease-in;
  width: 50px;
  height: 2px;
  background-color: #41544a;
  position: absolute;
  bottom: 0px;
  left: 0;
}
.promotion-small__background-2 {
  background: url(/source/images/small-promotion-background-2.svg) no-repeat;
}

.heading-section {
  font-size: 30px;
  font-weight: 600;
  text-transform: uppercase;
  display: flex;
  column-gap: 10px;
  justify-content: center;
  align-items: center;
}
.heading-section__badge {
  color: #fff;
  font-size: 12px;
  padding: 8px 30px;
  background-color: #86bc42;
  border-radius: 30px;
}

.ft-product-badge {
  display: flex;
  color: #4f4f4f;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 0.1em;
  column-gap: 41px;
}
.ft-product-badge__btn {
  transition: all 0.25s ease-out;
  text-transform: uppercase;
  padding: 8px 18px;
  border-radius: 30px;
}
.ft-product-badge__btn:hover {
  color: #fff;
  background-color: #41544a;
}
.ft-product-badge__btn--active {
  text-transform: uppercase;
  padding: 8px 18px;
  border-radius: 30px;
  color: #fff;
  background-color: #41544a;
}

.ft-product-card {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  color: #121212;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 11px 50px 30px 50px;
  transition: border-color 0.25s ease-out;
}
.ft-product-card__active {
  border: 1px solid #ebebeb;
  padding: 11px 30px 30px 30px;
}
.ft-product-card:hover {
  border-color: #ebebeb;
  padding: 11px 30px 30px 30px;
}
.ft-product-card:hover .ft-product-card__behaviour {
  transition: all 2s ease-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 22px;
  position: absolute;
  top: 9px;
  right: -30px;
}
.ft-product-card:hover .ft-product-card__price {
  display: none;
}
.ft-product-card:hover .ft-product-card__btn {
  display: flex;
  transition: all 3s ease-in;
  column-gap: 15px;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 45px;
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  background-color: #86bc42;
  border-radius: 4px;
}
.ft-product-card__image {
  position: relative;
}
.ft-product-card__title {
  font-size: 12px;
  font-weight: 700;
  color: #4f4f4f;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
.ft-product-card__star {
  font-size: 10px;
  color: #f2b23e;
}
.ft-product-card__star--grey {
  color: #ebebeb;
}
.ft-product-card__name {
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
}
.ft-product-card__price {
  font-size: 16px;
  font-weight: 700;
  color: #86bc42;
  height: 45px;
}
.ft-product-card__btn {
  display: none;
}
.ft-product-card__behaviour {
  display: none;
}
.ft-product-card__save {
  width: 39px;
  height: 39px;
  border: 1px solid #86bc42;
  border-radius: 50%;
  background-color: #fff;
  display: flex;
  justify-content: center;
}
.ft-product-card__price--active {
  display: none;
}
.ft-product-card__btn--active {
  display: flex;
  column-gap: 15px;
  justify-content: center;
  align-items: center;
  width: 240px;
  height: 45px;
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  background-color: #86bc42;
  border-radius: 4px;
}
.ft-product-card__behaviour--active {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 22px;
  position: absolute;
  top: 9px;
  right: -30px;
}
.ft-product-card__tag {
  display: flex;
  flex-direction: column;
  row-gap: 10px;
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  position: absolute;
  top: 9px;
  left: -33px;
}
.ft-product-card__common {
  padding: 5px 17px;
  border: 1px solid transparent;
  border-radius: 60px;
}
.ft-product-card__state {
  background-color: #f2b23e;
}
.ft-product-card__deal {
  background-color: #cf3e3b;
}

.order-service {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  font-size: 24px;
  font-weight: 600;
  color: #41544a;
  border: 1px dashed #86bc42;
  background-color: #fafaeb;
  padding: 41px 0;
  position: relative;
}
.order-service__delivery {
  position: absolute;
  left: 13px;
  top: -29px;
}
.order-service__phone {
  color: #86bc42;
}

.card-deal-day {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  color: #121212;
  border: 1px solid transparent;
  border-radius: 4px;
  padding: 11px 30px 30px 30px;
  transition: border-color 0.25s ease-out;
}
.card-deal-day__active {
  border: 1px solid #ebebeb;
  padding: 11px 30px 30px 30px;
}
.card-deal-day:hover {
  border-color: #ebebeb;
}
.card-deal-day__title {
  font-size: 12px;
  font-weight: 700;
  color: #4f4f4f;
  letter-spacing: 0.1em;
  text-transform: uppercase;
}
.card-deal-day__star {
  font-size: 10px;
  color: #f2b23e;
}
.card-deal-day__star--grey {
  color: #ebebeb;
}
.card-deal-day__name {
  font-size: 14px;
  font-weight: 400;
  text-transform: capitalize;
}
.card-deal-day__price {
  font-size: 16px;
  font-weight: 700;
  color: #86bc42;
  margin-top: 6px;
  margin-bottom: 20px;
}
.card-deal-day__state-bar {
  width: 240px;
  height: 6px;
  background-color: #ebebeb;
  border-radius: 30px;
  position: relative;
}
.card-deal-day__green-bar {
  width: 108px;
  height: 6px;
  background-color: #86bc42;
  border-radius: 30px;
  position: absolute;
  top: 0;
}
.card-deal-day__state-available {
  width: 240px;
  display: flex;
  justify-content: space-between;
  color: #4f4f4f;
  font-size: 14px;
  font-weight: 400;
}
.card-deal-day__state-number {
  font-weight: 700;
}

.nav-arrow {
  color: #121212;
  width: 45px;
  height: 45px;
  background-color: #fff;
  border: 1px solid #ebebeb;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.05);
  transition: all 0.25s ease-in;
}
.nav-arrow:hover {
  color: #86bc42;
  border-color: #86bc42;
  cursor: pointer;
}
.nav-arrow__active {
  color: #86bc42;
  border-color: #86bc42;
}
.nav-arrow__right {
  transform: rotate(180deg);
}

.hero-2 {
  background: url(/source/images/hero-2-background.svg);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-top: 68px;
  padding-bottom: 177px;
}
.hero-2__text-1 {
  font-size: 14px;
  font-weight: 700;
  color: #849d6a;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  margin-top: 36.5px;
}
.hero-2__text-2 {
  font-size: 60px;
  font-weight: 600;
  color: #41544A;
  text-transform: uppercase;
  margin: 20px 0;
}
.hero-2__text-3 {
  font-size: 14px;
  font-weight: 400;
  color: #4F4F4F;
  margin-bottom: 26px;
}
.hero-2__btn {
  font-size: 13px;
  font-weight: 700;
  color: #fff;
  background-color: #86BC42;
  padding: 17px 47px;
  border-radius: 4px;
}

.banner {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  column-gap: 175px;
  background-color: #fff;
  border-radius: 4px;
  height: 130px;
}

.blog {
  width: 410px;
  display: inline-flex;
  flex-direction: column;
}
.blog__image {
  position: relative;
}
.blog__date {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  column-gap: 4px;
  text-transform: uppercase;
  color: #121212;
  background-color: #fff;
  padding: 18px 13px 10px 13px;
  border-radius: 0 0 4px 4px;
  position: absolute;
  top: 0;
}
.blog__month {
  font-size: 12px;
  font-weight: 700;
}
.blog__day {
  font-size: 30px;
  font-weight: 600;
}
.blog__tag {
  color: #fff;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  background-color: #86bc42;
  border-radius: 60px;
  padding: 5px 17px;
  position: absolute;
  bottom: 0;
}
.blog__title {
  font-size: 18px;
  font-weight: 700;
  color: #121212;
}
.blog__author {
  display: flex;
  column-gap: 10px;
  font-size: 14px;
  font-weight: 400;
  color: #4f4f4f;
  text-transform: capitalize;
}
.blog__account {
  margin-right: 21px;
}
.blog__account:hover {
  text-decoration: underline;
}

@tailwind utilities;